import React from 'react'
import { Link } from 'react-router-dom'

function Header() {
  return (
    <header style = {headerStyle}>
      <h1>Notes List</h1>
      <Link style={linkStyle} to = "/">Home</Link> | 
      <Link style={linkStyle} to="/about">About</Link>
    </header>
  )
}

const headerStyle = {
 background: '#32A287',
 color: '#F1FFE7',
 textAlign: 'center',
 padding: '10px'
}

const linkStyle = {
  color: '#A9FDAC',
  textDecoration: 'none'
}

export default Header;