import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import './App.css';
import NotesList from './components/NotesList';
import AddNote from './components/AddNote';
import Header from './components/layout/header'
import about from './components/pages/about'
import {v4 as uuidv4} from 'uuid';

class App extends React.Component {
    state = {
            notesList: [{
                    id: uuidv4(),
                    title: 'Take out the thrash',
                    completed: false
                },
                {
                    id: uuidv4(),
                    title: 'Make Dinner',
                    completed: false
                },
                {
                    id: uuidv4(),
                    title: 'Meeting with boss',
                    completed: false
                }
            ]
        }
        // Mark Notes as Completed
    markComplete = (id) => {
        this.setState({
            notesList: this.state.notesList.map(note => {
                if (note.id === id) {
                    note.completed = !note.completed
                }
                return note
            })
        });
    }

    //Delete Notes
    deleteNote = (id) => {
        this.setState({ notesList: [...this.state.notesList.filter(note => note.id !== id)] })
    }

// Add Note

addNote = (title) => {
    const newNote = {
        id: uuidv4(),
        title, 
        completed: false
    }
this.setState({ notesList: [...this.state.notesList, newNote]})
}

    render() {
        return ( 
            <Router>
        <div className = "App">
            <div className="container">
                <Header/>
                <Route exact path = "/" render={props=>(
                    <React.Fragment>
                       <AddNote addNote = {this.addNote}/>
                         <NotesList notesList = { this.state.notesList }
                           markComplete = { this.markComplete }
                            deleteNote = { this.deleteNote }/> 
                    </React.Fragment>
                )}/>
                 <Route path = "/about" component = {about}/>
            </div>
        </div>
        </Router>
        );
    }
}

export default App;